.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}


@import url("https://fonts.googleapis.com/css2?family=Inter:wght@100;200;300;400;500;600;700;800;900&display=swap");
.base {
  margin: 0;
  padding: 0;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  font-family: "Inter", sans-serif;
}
.base h1 {
  font-size: 1.2rem;
}
.base .input-guess-base {
  width: auto;
  display: flex;
  align-items: center;
  justify-content: space-evenly;
  gap: 0.5rem;
}
.base .input-guess-base input {
  width: 1ch;
  padding: 1rem;
  border-top: 0px solid transparent;
  border-left: 0px solid transparent;
  border-right: 0px solid transparent;
  border-bottom: 3px solid rgba(0, 0, 0, 0.5);
  outline: none;
  font-size: 2rem;
  transition: all 0.2s ease;
}
.base .input-guess-base input:focus {
  border-bottom: 3px solid #574b90;
}
.base .button {
  visibility: hidden;
  position: absolute;
  margin-top: 2rem;
  right: 0;
  text-transform: uppercase;
  padding: 0.4rem;
  background: none;
  border: 1px solid #574b90;
  border-radius: 5px;
  cursor: pointer;
  transition: all 0.2s ease;
}

.button.button-history, .button.button-leaderboard {
  position: relative;
  margin-right: 20px;
}

.base .button.show {
  visibility: visible;
}
.base .button:hover {
  background: #574b90;
  color: #fff;
}
.base .guess-error {
  animation: shake 0.3s ease forwards;
  pointer-events: none;
}
.base .guess-error input {
  border-bottom: 2px solid red;
  pointer-events: none;
}
.base .guess-error input:focus {
  border-bottom: 2px solid red;
}
@keyframes shake {
  0% {
    transform: translateX(0);
}
  25% {
    transform: translateX(5px);
}
  50% {
    transform: translateX(-5px);
}
  75% {
    transform: translateX(5px);
}
  100% {
    transform: translateX(0);
}
}


.Leaderboard{
  padding: 40px 20px;
  margin: auto;
  max-width: 800px;
}
.leader-wrap {
  display: flex;
}

.leader{
  padding: 8px 16px;
  margin-bottom: 8px;
  animation-name: revealLeaders;
  animation-duration: .4s;
  animation-fill-mode: both;
  animation-timing-function: ease-in-out;
}

.leader-ava {
  padding: 8px;
  margin-right: 16px;
  position: relative;
}

.leader-score {
  display: flex;
  align-items: center;
  opacity: 0.6;
}

.leader-score svg{
  display: block;
  margin-right: 4px;
}

.leader-score_title{
  line-height: 1;
}

.leader-ava::after{
  content: "";
  left: 0;
  bottom: 0;
  display: block;
  height: 6px;
  position: absolute;
  border: 0px transparent solid;
  border-left-width: 20px;
  border-right-width: 20px;
  border-bottom-width: 6px;
  border-bottom-color: #fff;
  transition: border-bottom-color .2s ease-in-out;
}

.leader-bar {
  margin-top: 8px;
  animation-name: barLoad;
  animation-duration: .4s;
  animation-fill-mode: both;
  animation-timing-function: cubic-bezier(0.6, 0.2, 0.1, 1);
  transform-origin: left;
}

.bar {
  height: 4px;
  border-radius: 2px;
}

@keyframes revealLeaders{
  from{
      transform: translateX(-200px);
      opacity: 0;
  }
  to{
      transform: none;
      opacity: 1;
  }
}

@keyframes barLoad{
  from{
      transform: scaleX(0);
  }
  to{
      transform: scaleX(1)
  }
}

/* Styling for the leaderboard overlay */
.leaderboard-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5); /* Semi-transparent black background */
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1;
}

/* Styling for the content within the leaderboard overlay */
.leaderboard-content {
  background-color: white;
  padding: 20px;
  border-radius: 10px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.3);
  position: relative;
}

/* Styling for the close button */
.close-button {
  position: absolute;
  top: 10px;
  right: 10px;
  background-color: transparent;
  border: none;
  font-size: 20px;
  cursor: pointer;
}

/* history */

ol {
  /* background-color: #7162a6; */
  top: 20px;
  color: hsl(0 0% 95%);
  
  --ol-padding-block: 5rem;
  --side-padding: 1rem;
  --side-number-width: 2.5rem;
  --side-width: calc(var(--side-number-width) + var(--side-padding) * 2);
  --li-padding: 1rem;
  --li-spacing: 1rem;
  
  margin: 0;
  padding-inline-start: var(--side-width);
  padding-inline-end: var(--side-padding);
  padding-block: var(--ol-padding-block);
  display: grid;
  row-gap: var(--li-spacing);
  overflow: hidden;
  position: relative;
  counter-reset: liCount;
  list-style: none;
 
  
  &::before {
    content: "";
    position: absolute;
    width: var(--side-width);
    height: 100%;
    top: 0;
    left: 0;
    z-index: 1;
    box-shadow: 5px 0 20px hsl(0 0% 0% / .5);
  }
  > li {
    width: fit-content;
    padding: var(--li-padding);
    border-radius: 0 0.5rem 0.5rem 0;
    background-color: var(--accent-color, grey);
    background-image: linear-gradient(to left, transparent, hsl(0 0% 0% / .5));
    background-blend-mode: color-burn;
    --shadow: 2px 2px 5px hsl(0 0% 0% / .5);
    box-shadow: var(--shadow);
    position: relative;
    counter-increment: liCount;
    
    &::before {
      content: counter(liCount);
      position: absolute;
      width: var(--side-number-width);
      height: 100%;
      top: 0;
      right: calc(100% + (var(--side-width) - var(--side-number-width)) / 2);
      background-color: inherit;
      background-image: linear-gradient(to right, transparent, hsl(0 0% 0% / .5));
      background-blend-mode: inherit;
      border-radius: 0.25rem;
      box-shadow: inset var(--shadow);
      display: grid;
      place-items: center;
      font-size: 1.5em;
    }
  }
}

/* button */

.buttons {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-gap: 5px;
}

button {
  background-color: #fff;
  border: none;
  border-radius: 5px;
  padding: 15px;
  font-size: 1.5rem;
  cursor: pointer;
  transition: background-color 0.2s ease-in-out;
}

button:hover {
  background-color: #f5f5f5;
}

.enter {
  grid-column: span 2;
}